          { <span class="hljs-attr">lng</span>: -<span class="hljs-number">107.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">43.0</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">100000.0</span> }
