          { <span class="hljs-attr">lng</span>: -<span class="hljs-number">100.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">50.0</span> },
          { <span class="hljs-attr">lng</span>: -<span class="hljs-number">97.5</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">50.0</span> },
          { <span class="hljs-attr">lng</span>: -<span class="hljs-number">95.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">50.0</span> },
          { <span class="hljs-attr">lng</span>: -<span class="hljs-number">92.5</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">50.0</span> },
